<template>
  <div>
    <Form :model="formData" :label-width="80">
      <FormItem label="account">
        <Input type="text" v-model="formData.user" placeholder="Username" />
      </FormItem>
      <FormItem label="password">
        <Input
          type="password"
          v-model="formData.password"
          placeholder="Password"
        />
      </FormItem>
      <FormItem label="confirm password" v-if="registerShow">
        <Input
          type="password"
          v-model="formData.password1"
          placeholder="Password"
        />
      </FormItem>
      <FormItem label="administrators" v-if="registerShow">
        <Select placeholder="Select" v-model="formData.admin">
          <Option
            v-for="item in adminList"
            :value="item.value"
            :key="item.value"
            >{{ item.label }}</Option
          >
        </Select>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="loginIn" v-if="!registerShow"
          >Signin</Button
        >
        <Button type="default" @click="register" v-if="!registerShow"
          >register</Button
        >
        <Button type="primary" @click="Submit" v-if="registerShow"
          >Submit</Button
        >
        <Button type="default" @click="Cancel" v-if="registerShow"
          >Cancel</Button
        >
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  name: "login",
  data() {
    return {
      registerShow: false,
      formData: {},
      adminList: [
        { value: 0, label: "No" },
        { value: 1, label: "Yes" },
      ],
    };
  },
  methods: {
    loginIn() {
      let { user, password } = this.formData;
      this.$ajax
        .post("api/loginIn", { user, password })
        .then((resData) => {
          let data = resData.data;
          if (data === "success") {
            this.$router.push("/index/" + user);
          } else {
            this.$Message.error(data);
          }
        });
    },
    register() {
      this.registerShow = true;
    },
    Submit() {
      let { user, password, password1, admin = 0 } = this.formData;
      if (password !== password1) {
        this.$Message.error("The two passwords are different!");
      } else {
        this.$ajax
          .post("api/loginIn/addUser", { user, password, admin })
          .then((resData) => {
            let data = resData.data;
            if (data === "success") {
              this.$router.push("/index/" + user);
            } else {
              this.$Message.error(data);
            }
          });
      }
    },
    Cancel() {
      this.registerShow = false;
    },
  },
};
</script>